import imgItem from '@/colorui/components/image-item.vue'
import formatDate from '@/colorui/components/formatDate.vue'
import imtAudio from '@/colorui/components/imt-audio.vue'
import rootApi from '@/api/root.js'
import tokenApi from '@/api/token.js'
export default {
	components: {
		imgItem,
		imtAudio,
		formatDate
	},
	data() {
		return {
			list:[],
			id:'',
			index:null,
			item:{},
			title:'',
			nextLink:'',
			isLoad:false,
			modalName: null,
			src:'',
			duration:null
		}
	},
	onLoad(e) {
		this.title = e.name;
		this.id = e.id;
		this.getDrives(this.id)
	},
	// 下拉刷新
	onPullDownRefresh() {
		this.cleanDate();
		this.getDrives(this.id,"refresh")
	},
	// 上拉加载
	onReachBottom: function(){
		if(this.nextLink&&this.isLoad){
			this.isLoad = false;
			this.nextPage();
		}
	},
	methods: {
		cleanDate(){
			this.list=[];
			this.nextLink='';
		},
		copyAddress(){
			let item = this.list[this.index];
			tokenApi.urlCreate({
				Url:item['@microsoft.graph.downloadUrl'],
			}).then(res => {
				let url = item['@microsoft.graph.downloadUrl'];
				if(res.Code==0){
					url = res.ShortUrl;
				}
				uni.setClipboardData({
					data: url,
					success: function () {}
				});
				this.modalName =null;
					uni.showToast({
					title: "复制成功",
					icon: 'none'
				})
			})
		},
		downButton(){
			downloadFile(this.item);
		},
		openDocument(item){
			uni.openDocument({
			  filePath: item.filePath,
			  success: function (res) {
				
			  },
			  complete(){
					uni.hideLoading();
			  }
			});
		},
		downloadFile(item){
			this.modalName =null;
			const downloadTask = uni.downloadFile({
				url: item['@microsoft.graph.downloadUrl'],
				success: (res) => {
					if (res.statusCode === 200) {
						var filePath = res.tempFilePath;
						item.filePath = filePath;
						uni.openDocument({
						  filePath: filePath,
						  success: function (res) {
							
						  }
						});
					}
				},fail(){
					uni.showToast({
						title: "下载失败",
						icon: 'none'
					})
				},complete(){
					uni.hideLoading();
				}
			});
		
			downloadTask.onProgressUpdate((res) => {
				item.progress = res.progress;
			});
		},
		hideModal(e) {
			this.modalName = null
		},
		more(e){
			this.modalName="bottomModal";
			this.index = e.currentTarget.dataset.index;
			let index = this.index
			let item = this.list[index];
			this.item = e.item;
		},
		navigateTo(url){
			uni.navigateTo({
				url:url
			})
		},
		mp3Play(item){
			if(item['@microsoft.graph.downloadUrl']==this.src){
				  this.modalName="Modal";
				  return;
			}
			uni.showLoading({
				mask:true,
				title: '正在打开...'
			});
			  // 创建一个audio
			this.innerAudioContext = wx.createInnerAudioContext();
			// 设置audio的资源地址
			this.innerAudioContext.src = item['@microsoft.graph.downloadUrl']  
			// 监听音频进入可以播放状态的事件
			this.innerAudioContext.onCanplay(()=> {
			  // 必须。可以当做是初始化时长
			  this.innerAudioContext.duration;
			  // 必须。不然也获取不到时长
			  setTimeout(() => {
				  this.src= item['@microsoft.graph.downloadUrl'];
				  this.duration = this.innerAudioContext.duration;
				  this.modalName="Modal";
				  uni.hideLoading();
			  }, 200)
			})  
		},
		onClick(e){
			this.index = e.currentTarget.dataset.index;
			let index = this.index
			let item = this.list[index];
			let type = this.fileType(item)
			if(type=='pptx'||type=='xlsx'||type=='word'||type=='pdf'){
				type ="office";
			}
			switch(type) {
				 case "folder":
					this.navigateTo("/pages/index/folder?id="+item.id+"&name="+item.name);
					break;
				 case "video":
				   	this.showModel();
				    return; 
				 	this.$store.commit("setItem",item);
					this.navigateTo("/pages/index/video?id="+item.id+"&name="+item.name);
					break;
				case "image":
					this.previewImage(item);
					break;
				case "mp3":
					this.mp3Play(item);
					break;
				case "office":
					uni.showLoading({
						mask:true,
						title: '正在打开...'
					});
					if(item.filePath){
						this.openDocument(item);
					}else{
						this.downloadFile(item);
					}
					break;
				 default:
					this.showModel();
			} 
		},
		previewImage(item){
			 // 预览图片
			uni.previewImage({
				urls: [item['@microsoft.graph.downloadUrl']],
				longPressActions: {
					itemList: ['发送给朋友', '保存图片', '收藏'],
					success: function(data) {

					},
					fail: function(err) {

					}
				}
			});
		},
		nextPage(){
			const _this =this;
			rootApi.nextPage(this.nextLink).then(res => {
				_this.list =_this.list.concat(res.value);
				_this.nextLink = res['@odata.nextLink'];
				this.isLoad = true;
			})
		},
		showModel(){
			uni.showModal({
				title: '提示',
				showCancel:false,
				content: '目前仅支持PPT,Word,Excel,Pdf,图片.',
				success: function (res) {
					if (res.confirm) {

					}
				}
			});
		},
		getDrives(id,type){
			const _this =this;
			this.isLoad = false;
			rootApi.get_drives(id).then(res => {
				_this.list = _this.list.concat(res.value);
				_this.nextLink = res['@odata.nextLink'];
				this.isLoad = true;
				if(type=="refresh"){
					uni.hideNavigationBarLoading();
					uni.stopPullDownRefresh();//得到数据后停止下拉刷新
				}
			})
		}
	}
}
